


















































import {Vue, Component} from 'vue-property-decorator';
import {Panel, Button, CellGroup, Cell, Toast} from 'vant';
import {notes, NoteType} from '@/util/note';
import 'vant/lib/panel/style';
import 'vant/lib/button/style';
import 'vant/lib/cell/style';
import 'vant/lib/cell-group/style';
import 'vant/lib/toast/style';
import ScanSign from '@/componets/scan_sign.vue';
import {
    AuthOriginalTxModel, AuthAdminType, default as OriginalAddressModel
} from '../../model/original_address_model';
import {CashboxJs} from '@/util/cashboxjs';
import {DiamondUtil} from "../../util/diamond_util";
import {ToastUtil} from "../../util/toast";
import GlobalConfig from "../../config/global_config";

Vue.use(Panel).use(Button).use(Cell).use(CellGroup).use(Toast);

@Component({
    components: {ScanSign},
})

export default class Sign extends Vue {
    private qrCodeText = '';                // 二维码内容
    private authTypeValue = "";             // 授权或取消
    private isLoadingAccount = true;
    private txAddress = "";                 // 操作申请者 地址
    private txName = "";                    // 操作申请者 用户名
    private nowApplyName = '';              // 授权管理员 用户名
    private nowApplyAddress = '';           // 授权管理员 地址
    private waitSignInfo = '';              // 拼接 待签名信息
    private isShowQrLayout = false;         // 是否显示二维码区域

    public created(): void {
        this.$store.state.title = '信息签名';
        {
            (window as any).nativeQrScanToJsResult = this.nativeQrScanToJsResult; // 注册 调用原生方法 返回调用方法
        }
        if (this.$store.state.authOriginalTxModel.authType == AuthAdminType.DeleteAdmin) {
            this.authTypeValue = this.$t('cancelOriginal').toString();
        } else {
            this.authTypeValue = this.$t('authOriginal').toString();
        }
    }

    public async mounted() {
        const authOriginalTxModel: AuthOriginalTxModel = this.$store.state.authOriginalTxModel;
        this.nowApplyName = authOriginalTxModel.nowAdminName;
        this.nowApplyAddress = authOriginalTxModel.nowAdminAddress;
        this.txAddress = authOriginalTxModel.txAddress;
        this.txName = authOriginalTxModel.txName;
        await this.loadWaitSignInfo();
        this.isLoadingAccount = false;
        {   //二维码规则：    1、时间戳有效期暂定+半小时       2、操作类型dtt=01（签名操作管理员）
            this.qrCodeText = 'https://cashbox.scry.info/qr?ct=66&ot=ds&dt=0&dtt=' + GlobalConfig.QR_PARAM_ADMIN + '&v=' + this.waitSignInfo +
                '&tl=' + GlobalConfig.QR_USEFUL_TIME() + '&';
            console.log("result qrCodeText info is===>" + this.qrCodeText)
        }
    }

    public async loadWaitSignInfo() {
        if (this.$store.state.authOriginalTxModel.authType == AuthAdminType.DeleteAdmin) {
            this.waitSignInfo = await DiamondUtil.getInstance().assembleDeleteAdminSingleMessage(this.nowApplyAddress, this.txAddress);//
        } else {
            this.waitSignInfo = await DiamondUtil.getInstance().assembleAddAdminSingleMessage(this.nowApplyAddress, this.txAddress,
                this.txName);//
        }

        if (!this.waitSignInfo || this.waitSignInfo === "") {
            console.log("!this.waitSignInfo is:" + !this.waitSignInfo);
            this.waitSignInfo = "signIs error";
            ToastUtil.show("签名获取到的二维码信息错误，请重新尝试签名");
        } else {
            console.log("!this.waitSignInfo is:" + !this.waitSignInfo);
            this.isShowQrLayout = true;
        }
    }

    public scanGetSigned() {
        CashboxJs.getInstance().callNativeQrScanToJs();
    }

    //解析扫描二维码后的信息结果
    public nativeQrScanToJsResult(scannedValue: string) {
        // 根据二维码签名格式，筛选获取签名部分信息
        const paramsList: string[] = scannedValue.split(';');
        // let paramsMap: Map = new Map<string, string>();
        let paramsMap: { [key: string]: string } = {};
        for (let i = 0; i < paramsList.length; i++) {
            const index = i;
            const tagIndex = paramsList[index].indexOf('=');
            if (tagIndex !== -1) {
                paramsMap[paramsList[index].substring(0, tagIndex)] = paramsList[index].substring(tagIndex + 1);
            }
        }
        let signedValue = '';
        if (paramsMap.dtt && paramsMap.dtt == '01' && paramsMap.v) {
            signedValue = paramsMap.v;
        } else {
            ToastUtil.show('二维码信息有问题:' + scannedValue);
            return;
        }
        {
            let addressModel = new OriginalAddressModel();
            addressModel.address = this.nowApplyAddress;     //记录管理员（原始地址）签名结果信息，到TxModel上
            addressModel.signInfo = signedValue;
            this.$store.commit('updateAdminAuthStatus', addressModel);
        }
        this.$router.push({name: 'original_state_list'});
    }
}
